@import "../../global.scss";

.menu {
  width: 300px;
  height: 100vh;
  background-color: $mainColor;
  position: fixed;
  top: 0;
  right: -300px;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: all 1s ease;

  @include mobile{
    width: 245px;
  }

  &.active {
    right: 0;
  }

  ul {
    margin: 0;
    padding: 0;
    list-style: none;
    font-size: 25px;
    font-weight: 300;
    color: white;
    width: 60%;

    a {
      font-size: inherit;
      color: inherit;
      text-decoration: none;
      li {
        margin-bottom: 25px;
        &:hover {
          font-weight: 500;
        }
      }
    }
  }
}